import PressButton from 'src/packages/press-button/press-button.vue';
import PressField from 'src/packages/press-field/press-field.vue';


export default {
  i18n: {
    'zh-CN': {
      custom: '自定义',
      mockData: '自定义数据',
    },
    'en-US': {
      custom: 'Custom',
      mockData: 'Custom Data',
    },
  },
  components: {
    PressField,
    PressButton,
  },
  props: {
    value: {
      type: String,
      default: '',
    },
    sectionStyle: {
      type: String,
      default: 'margin: 0;background: #f7f8fa;',
    },
    headerStyle: {
      type: String,
      default: 'background: #f7f8fa;',
    },
    autosize: {
      type: [Boolean, Object],
      default: () => ({ maxHeight: 600, minHeight: 50 }),
    },
  },
  data() {
    return {
      inputValue: this.value,
    };
  },
  watch: {
    value: {
      handler(val) {
        this.inputValue = val;
      },
    },
  },
  mounted() {

  },
  methods: {
    onClearInput() {
      this.inputValue = '';
    },
    onConfirmInput() {
      this.$emit('confirm', this.inputValue);
    },
  },
};
